@font-face {
  font-family: Nunito;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Nunito-400.woff2") format("woff2"), url("../fonts/Nunito-400.woff") format("woff");
}
@font-face {
  font-family: Nunito;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/Nunito-600.woff2") format("woff2"), url("../fonts/Nunito-600.woff") format("woff");
}
@font-face {
  font-family: Nunito;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Nunito-700.woff2") format("woff2"), url("../fonts/Nunito-700.woff") format("woff");
}
@font-face {
  font-family: Comfortaa;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Comfortaa-700.woff2") format("woff2"), url("../fonts/Comfortaa-700.woff") format("woff");
}
html {
  box-sizing: border-box;
  position: relative;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: 320px;
  min-height: 100vh;
  font-family: Nunito, Arial, sans-serif;
  font-size: 18px;
  font-weight: 400;
  background-color: #FFFFFF;
  color: #210B36;
  margin: 0;
  overflow-x: hidden;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  font-family: Nunito, Arial, sans-serif;
}

a {
  color: inherit;
  text-decoration: none;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, p, figure, fieldset {
  margin: 0;
}

.container {
  max-width: 1440px;
  padding: 0 130px;
  margin: 0 auto;
}
@media (max-width: 1150px) {
  .container {
    max-width: 1024px;
    padding: 0 22px;
  }
}
@media (max-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 94px;
  }
}
@media (max-width: 520px) {
  .container {
    max-width: 320px;
    padding: 0 15px;
  }
}

section {
  padding: 70px 0;
}
@media (max-width: 768px) {
  section {
    padding: 60px 0;
  }
}
@media (max-width: 520px) {
  section {
    padding: 40px 0;
  }
}

.hide {
  display: none;
}

svg path {
  fill: inherit;
  stroke: inherit;
  stroke-width: inherit;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.header {
  position: relative;
  height: 38px;
  z-index: 10;
  background-color: transparent;
  margin-bottom: -38px;
  padding-top: 20px;
  z-index: 110;
}
.header__container {
  display: flex;
  gap: 50px;
  justify-content: space-between;
}
.header__logo-link {
  display: flex;
  gap: 10px;
  align-items: center;
  font-size: 22px;
  font-weight: 600;
  line-height: 30px;
}
.header__logo {
  width: 38px;
  height: 38px;
}
.header__nav {
  display: flex;
  align-items: center;
}
@media (max-width: 768px) {
  .header__nav {
    display: none;
  }
}
.header__list {
  display: flex;
  gap: 40px;
  color: #FFFFFF;
  font-size: 15px;
  font-weight: 700;
  line-height: 20.46px;
}
@media (min-width: 2100px) {
  .header__list {
    color: #210B36;
  }
}
.header__button-nav {
  display: none;
}
@media (max-width: 768px) {
  .header__button-nav {
    display: flex;
    align-self: center;
    width: 24px;
    height: 24px;
    background: transparent;
    border: none;
    outline: none;
    padding: 0;
  }
}
.header__button-icon, .header__button-close {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.header__button-close:hover {
  border-radius: 50%;
  background: #DFD4EB;
}
.header__button-icon_hide, .header__button-close_hide {
  display: none;
}

.present {
  padding-top: 99px;
  padding-bottom: 52px;
}
@media (max-width: 1150px) {
  .present {
    padding-top: 113px;
    padding-bottom: 40px;
  }
}
@media (max-width: 768px) {
  .present {
    padding-top: 94px;
    padding-bottom: 36px;
  }
}
@media (max-width: 520px) {
  .present {
    padding-top: 88px;
    padding-bottom: 15px;
  }
}
.present__container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 20px;
}
@media (max-width: 768px) {
  .present__container {
    flex-direction: column;
  }
}
.present__block {
  padding-top: 130px;
  display: flex;
  flex-direction: column;
  flex-basis: 535px;
}
@media (max-width: 768px) {
  .present__block {
    padding-top: 0;
    align-items: center;
    order: 2;
    flex-basis: 0;
  }
}
.present__title {
  font-family: Comfortaa, Arial, sans-serif;
  font-size: 40px;
  font-weight: 700;
  line-height: 44.6px;
  margin-bottom: 20px;
  white-space: nowrap;
}
@media (max-width: 660px) {
  .present__title {
    font-size: 28px;
  }
}
@media (max-width: 520px) {
  .present__title {
    font-size: 19px;
    line-height: 21.19px;
    margin-bottom: 10px;
  }
}
.present__text {
  max-width: 380px;
  font-size: 18px;
  font-weight: 400;
  line-height: 23.4px;
  margin-bottom: 50px;
}
@media (max-width: 768px) {
  .present__text {
    text-align: center;
  }
}
@media (max-width: 660px) {
  .present__text {
    font-size: 16px;
  }
}
@media (max-width: 520px) {
  .present__text {
    font-size: 14px;
    line-height: 18.2px;
    margin-bottom: 30px;
  }
}
.present__btn-link {
  width: 380px;
  height: 70px;
  background: linear-gradient(87.4deg, #2e00b1 -11.74%, #8100bd 48.81%, #a400be 113.27%);
  border-radius: 10px;
  color: #FFFFFF;
  text-align: center;
  vertical-align: middle;
  font-size: 18px;
  font-weight: 700;
  line-height: 70px;
}
.present__btn-link:hover, .present__btn-link:active {
  background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1)), linear-gradient(87.4deg, #2E00B1 -11.74%, #8100BD 48.81%, #A400BE 113.27%);
}
.present__btn-link:focus {
  background-image: linear-gradient(0deg, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.2)), linear-gradient(87.4deg, #2E00B1 -11.74%, #8100BD 48.81%, #A400BE 113.27%);
}
@media (max-width: 660px) {
  .present__btn-link {
    width: 300px;
  }
}
@media (max-width: 520px) {
  .present__btn-link {
    font-size: 16px;
    line-height: 50px;
    width: 100%;
    height: 50px;
  }
}
.present__block-img {
  max-width: 549px;
}
@media (max-width: 1150px) {
  .present__block-img {
    max-width: 455px;
  }
}
@media (max-width: 768px) {
  .present__block-img {
    order: 1;
    margin-bottom: 14px;
  }
}
@media (max-width: 520px) {
  .present__block-img {
    max-width: 290px;
  }
}
.present__img {
  max-width: 100%;
  height: auto;
}

.footer {
  padding-top: 69px;
