  margin-bottom: 25px;
}
@media (max-width: 1150px) {
  .footer__block {
    margin-bottom: 4px;
  }
}
@media (max-width: 768px) {
  .footer__block {
    flex-direction: column;
    gap: 28px;
  }
}
@media (max-width: 520px) {
  .footer__block {
    gap: 32px;
  }
}
.footer__block-nav {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  padding-top: 8px;
}
@media (max-width: 1150px) {
  .footer__block-nav {
    flex-basis: 420px;
  }
}
@media (max-width: 768px) {
  .footer__block-nav {
    flex-basis: auto;
    padding-top: 0;
    margin-bottom: 8px;
  }
}
@media (max-width: 520px) {
  .footer__block-nav {
    flex-direction: column;
