@media (max-width: 1150px) {
  .footer__logo-block {
    flex-basis: 164px;
  }
}
@media (max-width: 768px) {
  .footer__logo-block {
    flex-basis: auto;
  }
}
.footer__logo-link {
  display: flex;
  gap: 10px;
  align-items: center;
  font-size: 22px;
