.burger {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  visibility: hidden;
  overflow: auto;
  z-index: 90;
}
.burger_visible {
  visibility: hidden;
  opacity: 0;
}
@media (max-width: 768px) {
  .burger_visible {
    opacity: 1;
    visibility: visible;
  }
}
.burger_visible .burger__container {
  opacity: 1;
  z-index: 95;
}
.burger__container {
  position: absolute;
  width: 100%;
  height: 230px;
  left: 0;
  top: 0;
  opacity: 0;
  background-color: #FFFFFF;
  transition: 0.5s ease-in-out;
  padding: 94px 94px 34px 94px;
  border-bottom: 2px solid #210B36;
  border-image: linear-gradient(87.4deg, #2e00b1 -11.74%, #8100bd 48.81%, #a400be 113.27%);
  border-image-slice: 1;
}
@media (max-width: 520px) {
  .burger__container {
    padding: 94px 15px 34px 15px;
  }
}
.burger__list {
  display: flex;
  flex-direction: column;
  font-size: 15px;
  font-weight: 700;
  line-height: 20.46px;
  gap: 20px;
}
.burger__item {
  height: 20px;
}

.present:after {
  content: "";
  width: 916px;
  height: 605px;
  position: absolute;
  background-image: url("../img/present/bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  top: 0;
  right: 0;
  z-index: -1;
}
@media (max-width: 1150px) {
  .present:after {
    width: 758px;
    height: 497px;
    background-image: url("../img/present/bg1024.png");
  }
}
@media (max-width: 768px) {
  .present:after {
    width: 535px;
